.header {
	padding-top: 44px;
	@include clr;
	&__logo {
		float: left; }

	.header__lang {
		float: right;
		margin-right: 50px;
		margin-top: 17px;
		font-family: "Proxima Nova";
		font-size: 15px;
		font-weight: 600;
		text-transform: uppercase;
		.lang__item {
			display: inline-block;
			margin-left: 13px; }
		.lang__link {
			display: block;
			color: #a38256; }
		.lang__link.is-active {
			color: #fff; } } }
.menu__button {
	background: transparent;
	border: none;
	float: right;
	margin-top: 14px;

	.burger {
		box-sizing: border-box;
		display: block;
		position: relative;
		border-radius: 10px;
		border: 10px solid #fff;
		border-top: 3px solid;
		border-bottom: 0px solid;
		margin: 4px 0; } }

.logo {
	display: block;
	@include s(logo-p);
	padding-left: 70px;
	color: #fff;
	font-size: 2.0625em;
	font-weight: 500;
	line-height: 1.55em;
	&:hover {
		text-decoration: none;
		color: #fff; } }
